.confirm-sub-card {
  border-radius: 8px !important;
  box-shadow: none !important;
}
#light .confirm-sub-card {
  background-color: #e9e9eb !important;
}
#dark .confirm-sub-card {
  background-color: #373947 !important;
}

#light #sen_lp .content {
  color: black;
}
#light #sen_lp .lp-card {
  background: #fff !important;
}
#light #sen_lp .deposit-card {
  background: #e9e9eb !important;
}
#light #sen_lp .order-card-bg {
  background-color: #ffffff !important;
}
#light #sen_lp .expand-card-bg {
  background-color: #e9e9eb !important;
}

#dark #sen_lp .content {
  color: white;
}
#dark #sen_lp .lp-card {
  background: #2c2e3d !important;
}
#dark #sen_lp .deposit-card {
  background: #2c2e3d;
}
#dark #sen_lp .order-card-bg {
  background-color: #2c2e3d !important;
}
#dark #sen_lp .expand-card-bg {
  background-color: #1d1f29 !important;
}

.card-active {
  border-color: rgba(249, 87, 94, 0.5) !important;
  cursor: pointer;
}
.caption {
  font-size: 12px;
}
.side-bar {
  min-height: 80px;
  padding: 24px;
}
.side-bar .header-sidebar {
  padding: 0 4px;
  width: 100%;
  border-radius: 8px;
}
.side-bar .header-sidebar .sentre-select-selector {
  width: 100% !important;
  background-color: transparent !important;
  box-shadow: none !important;
}
.side-bar .header-sidebar .sentre-select-selector:hover .side-bar .header-sidebar .sentre-select-selector:focus {
  border: solid 1px #e9e9eb !important;
}
.side-bar .header-sidebar .sentre-select-arrow {
  color: #7a7b85 !important;
}
.side-bar .body-sidebar {
  max-height: calc(100vh - 204px);
}
.widget .header-widget {
  padding: 0 4px;
  position: relative;
  top: -16px;
  left: -4px;
  width: calc(100% + 32px);
  background-color: #40a9ff;
  border-radius: 16px;
  height: 64px;
}
.widget .header-widget .sentre-select-selector {
  color: #e9e9eb !important;
  width: 151px !important;
  background-color: transparent !important;
  border: solid 1px #66baff !important;
  box-shadow: none !important;
}
.widget .header-widget .sentre-select-selector:hover .widget .header-widget .sentre-select-selector:focus {
  border: solid 1px #e9e9eb !important;
}
.widget .header-widget .sentre-select-arrow {
  color: #b3ddff !important;
}
.widget .body-widget .sentre-select-selection-item {
  color: #e9e9eb !important;
}
.widget .body-widget .expandHidden {
  display: none;
}
.widget .body-widget .sentre-collapse-header {
  display: none !important;
}
.widget .body-widget .sentre-collapse-content-box {
  padding: 0 !important;
}
.deposit-tag {
  position: relative;
  background: #f9575e1a !important;
  color: #f9575e !important;
  border-radius: 4px !important;
  border: none !important;
}
.deposit-tag::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  top: 50%;
  transform: translateY(-50%);
  left: -4px;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-right: 4px solid #f9575e1a;
}
.lp-card,
.order-card,
.deposit-card {
  box-shadow: unset !important;
  border-radius: 8px !important;
}
.order-card {
  position: relative !important;
  z-index: 1 !important;
}
.expand-card {
  position: relative;
  top: -24px;
  border: none !important;
  border-radius: 0 0 8px 8px !important;
  z-index: 0;
}
.expand-card .sentre-collapse-header {
  padding: 0 !important;
}
.expand-card .sentre-collapse-item {
  border: none !important;
}
.expand-card .sentre-collapse-content {
  background-color: transparent !important;
}
.expand-card .sentre-collapse-content-box {
  padding: 24px 12px 12px 12px !important;
}

